.products {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: 20px;
}

@media screen and (min-width: 768px) {
  .products {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 1280px) {
  .products {
    grid-template-columns: repeat(6, 1fr);
  }
}